import { useState } from "react";
import { useTodoDispatch } from "../context/TodoContext.js";
import { todoApi } from "../Api/TodoApi.js";

export const Form = _ => {

  const [ enteredTodo, setEnteredTodo ] = useState("");

  const dispatch = useTodoDispatch();

  const addTodo = e => {
    e.preventDefault();
    const newTodo = {
      id: Math.floor(Math.random() * 1e5),
      content: enteredTodo,
      editing: false
    };
    todoApi.post(newTodo)
    .then(response => {
      const todo = response[0];
      const todoEditing = Boolean(response[0].editing);
      const newTodo = { ...todo, editing: todoEditing };
      dispatch({ type: "todo/add", todo: newTodo });
    });
    setEnteredTodo("");
  };

  return (
    <div className="add-form">
      <form
        onSubmit={ addTodo }
      >
        <input
          type="text"
          value={ enteredTodo }
          onChange={ e => setEnteredTodo(e.target.value) }
        />
        <button
          type="submit"
          style={{ marginLeft: "5px" }}
        >add</button>
      </form>
    </div>
  );
};